.RegistrationForm__container {
  position: relative;
  border-radius: 20px;
  min-height: 541px;
  width: 554px;
  background-color: #fff;
  padding: 29px 98px;
}


.RegistrationForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.RegistrationForm__heading {
  text-align: center;
  font-size: 36px;
  margin: 0 0 20px;
}

.RegistrationForm__content {
  display: flex;
  flex-direction: column;
}

.RegistrationForm__registered {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
}

.RegistrationForm__text {
  color: #828282;
  margin: 0 5px 0 0;
}